import Hero from "../Components/Hero"
import Section from "../Components/Section"
import Headers from "../Components/Headers"
import Paragraph from "../Components/Paragraph"
import Color from "../Components/Color"


function Home() {
    return (
        <>
            <Hero />
            <Section title="01. Typography">
                <Headers />
                <Paragraph />
            </Section>
            <Section title="02. Color">
                <div class="color">
                    <div class="box white"></div>
                    <div class="box black-19"></div>
                    <div class="box black-39"></div>
                    <div class="box black-59"></div>
                    <div class="box black-79"></div>
                    <div class="box black-100"></div>
                </div>
                <div class="color">
                    <div class="box primary-light"></div>
                    <div class="box primary"></div>
                    <div class="box primary-dark"></div>
                </div>
            </Section>
        </>
        )
}

export default Home